<template>
  <div class="Journal-box">
     <!-- <div style="margin-bottom:50px ;">
        <el-page-header @back="goBack" content=""> </el-page-header>
      </div> -->
    <div class="Journal">
      <div class="Journal-img">
        <img v-if="JournalInfos.cover" :src="'./static/image/' + JournalInfos.cover" alt="" />
      </div>
      <div class="Journal-content">
        <p>{{ JournalInfos.title }}</p>
        <p>{{ JournalInfos.describe }}</p>
        <div>
          <!-- <a :href="'./static/' + JournalInfos.fileName">{{
              $t("Journal.button")
            }}</a> -->
          <el-button type="danger" round class="download" @click="downloadPDF(JournalInfos.fileName)">

            {{ $t("Journal.button") }}
          </el-button>
        </div>
      </div>
    </div>
    <div class="Journal-list">
      <p class="Journal-text">{{ $t("Journal.text") }}</p>
      <div class="Journal-view">
        <div class="Journal-item" v-for="(item, index) in JournalList" :key="index" @click="onDateils(item)">
          <img :src="'./static/image/' + item.img" alt="" />
          <div>
            <p :style="item.status == 0 ? 'background: rgba(0,0,0,0.5);' : ''">
              {{ item.count }}
            </p>
            <p>{{ item.title }}</p>
            <p>{{ item.describe }}</p>
            <p>{{ item.more }}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data: () => {
    return { JournalInfos: {}, JournalList: [],list:[] };
  },

  watch: {
    $route: {
      handler(to, from) {

        const id = this.$route.query.id;
        if (id) {
         this.intData(id)
        }
      },
      immediate: true,
    },
  },

  created() {
    const id = this.$route.query.id;
    if (id) {
      this.intData(id)
    }
  },
  methods: {
    goBack() {
      this.$router.back()
    },
    intData(id) {
      const lang = localStorage.getItem("lang");
      switch (lang) {
        case 'en':
          axios.get("/static/lang/en.json", {})
            .then(({ data }) => {
              console.log('获取到的json数据', data);
              this.list = data.JournalList
              const infos = this.list.filter((item) => item.id == id);
              if (infos.length > 0) {
                this.JournalInfos = infos[0];
              }
              this.JournalList = this.list.filter((item) => item.id != id);
              console.log( this.JournalList)
            })
          break;
        case 'Czh':
          //获取数据（访问路径不用带public ，直接访问public下的文件即可）
          axios.get("/static/lang/Czh.json", {})
            .then(({ data }) => {
              console.log('获取到的json数据', data);
              this.list = data.JournalList
              const infos = this.list.filter((item) => item.id == id);
              if (infos.length > 0) {
                this.JournalInfos = infos[0];
              }
              this.JournalList = this.list.filter((item) => item.id != id);
            })

          break;

        default:
          //获取数据（访问路径不用带public ，直接访问public下的文件即可）
          axios.get("/static/lang/zh.json", {})
            .then(({ data }) => {
              console.log('获取到的json数据', data);
              this.list = data.JournalList
              const infos = this.list.filter((item) => item.id == id);
              if (infos.length > 0) {
                this.JournalInfos = infos[0];
              }
              this.JournalList = this.list.filter((item) => item.id != id);
            })

          break;
      }

    },
    onDateils(item) {
      window.scrollTo({ top: 0 });
      if (item.status == 1) {
        this.$router.push({ path: "/Journal", query: { id: item.id } });
      } else {
        this.$router.push("/journalform");
      }
    },
    downloadPDF(fileName) {
      // 要下载的文件路径
      var fileUrl = './static/' + fileName;
      // 创建一个隐藏的<a>元素，用于下载文件
      var a = document.createElement('a');
      a.href = fileUrl;
      a.download = fileName; // 可以自定义下载文件的名称
      a.style.display = 'none';
      // 将<a>元素添加到页面上
      document.body.appendChild(a);
      // 触发点击事件，开始下载
      a.click();
      // 下载完成后，移除<a>元素
      document.body.removeChild(a);
    }
  },
};
</script>
